body {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.clock {
  border: 5px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.clock__hours {
  transform-origin: bottom;
  background-color: #00f;
  width: 2vmin;
  height: 20vmin;
  animation: 60s linear infinite movement;
  position: absolute;
  top: 20vmin;
}

.clock__minutes {
  transform-origin: bottom;
  background-color: green;
  width: 2vmin;
  height: 40vmin;
  animation: 5s linear infinite movement;
  position: absolute;
  top: 0;
}

.clock__center {
  background-color: #ff0;
  border-radius: 50%;
  width: 4vmin;
  height: 4vmin;
  position: relative;
}

@keyframes movement {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

